import Swiper, { Navigation, Pagination, Autoplay, Thumbs } from "swiper";
Swiper.use([Navigation, Pagination, Autoplay, Thumbs]);
const heroSlider = document.querySelector(".hero__slider");
const portfolioSlider = document.querySelector(".portfolio__slider");
const clientsSlider = document.querySelector(".clients__slider");
const workSlider = document.querySelector(".work-slider");
const historySlider = document.querySelector(".history__slider");
if (heroSlider) {
  const heroSliderSpeed = 1500;
  const bodyStyles = window.getComputedStyle(document.body);
  const fooBar = bodyStyles.getPropertyValue("--hero-slider-speed"); //get
  document.body.style.setProperty(
    "--hero-slider-speed",
    heroSliderSpeed + "ms"
  ); //set
  const mainHero = new Swiper(heroSlider, {
    slidesPerView: "auto",
    speed: heroSliderSpeed,
    autoplay: {
      delay: 3000,
    },
    navigation: {
      nextEl: ".hero__nav--next",
      prevEl: ".hero__nav--prev",
    },
    pagination: {
      el: ".swiper-pagination",
      type: "bullets",
      clickable: true,
    },
    on: {
      init: function () {
        const paginationBullets = document.querySelectorAll(
          ".hero__pag .swiper-pagination-bullet"
        );

        paginationBullets.forEach((el) => {
          el.innerHTML = `<span class="hero__bar"></span>`;
        });
      },
    },
  });
}
if (portfolioSlider) {
  const portfolio = new Swiper(portfolioSlider, {
    slidesPerView: 3,
    spaceBetween: 30,
    loop: true,
    navigation: {
      nextEl: ".portfolio__next",
      prevEl: ".portfolio__prev",
    },
    breakpoints: {
      // when window width is >= 320px
      200: {
        slidesPerView: 1,
        spaceBetween: 20,
      },
      // when window width is >= 480px
      510: {
        slidesPerView: 2,
        spaceBetween: 20,
      },
      // when window width is >= 640px
      900: {
        slidesPerView: 3,
        spaceBetween: 40,
      },
    },
  });
}
if (clientsSlider) {
  const clients = new Swiper(clientsSlider, {
    slidesPerView: 1,
    spaceBetween: 30,
    loop: true,
    navigation: {
      nextEl: ".clients__nav-prev",
      prevEl: ".clients__nav-next",
    },
  });
}
if (workSlider) {
  let workNav = new Swiper(".work-slider-nav", {
    spaceBetween: 20,
    freeMode: true,
    watchSlidesProgress: true,
    breakpoints: {
      // when window width is >= 320px
      320: {
        slidesPerView: 3,
        spaceBetween: 20,
      },
      // when window width is >= 480px
      480: {
        slidesPerView: 4,
        spaceBetween: 20,
      },
      // when window width is >= 640px
      640: {
        slidesPerView: 5,
        spaceBetween: 20,
      },
      992: {
        slidesPerView: 10,
        spaceBetween: 20,
      },
    },
  });
  let workSlide = new Swiper(workSlider, {
    slidesPerView: 1,
    spaceBetween: 20,
    navigation: {
      nextEl: ".work-inner-img__next",
      prevEl: ".work-inner-img__prev",
    },
    thumbs: {
      swiper: workNav,
    },
  });
}
if (historySlider) {
  const history = new Swiper(historySlider, {
    slidesPerView: 1,
    spaceBetween: 30,
    navigation: {
      nextEl: ".history__next",
      prevEl: ".history__prev",
    },
  });
  history.on("slideChange", function () {
    console.log(history.realIndex);
    navBtn.forEach((el) => {
      el.classList.remove("history__btn--active");
    });
    document
      .querySelector(`.history__btn[data-index="${history.realIndex}"]`)
      .classList.add("history__btn--active");
  });
  const navBtn = document.querySelectorAll(".history__btn");
  navBtn.forEach((el, idx) => {
    el.setAttribute("data-index", idx);
    el.addEventListener("click", (e) => {
      const index = e.currentTarget.dataset.index;
      navBtn.forEach((el) => {
        el.classList.remove("history__btn--active");
      });
      e.currentTarget.classList.add("history__btn--active");
      history.slideTo(index);
    });
  });
}
